import React, { Component } from 'react';

export default class Menu extends Component {

	shouldComponentUpdate = nextProps => {
		if(nextProps.classes !== this.props.classes) return true;
		else return false;
	}
	
	render = () => {
		const { classes, page, nav } = this.props;
		const d = classes === "menu" ? 0 : 0.3;
		return (
			<section className={classes}>
				<div className="panel panel1"></div>
				<div className="panel panel2"></div>
				<div className='links'>
					<div 
						className={page === 'Home' ? 'link on-page' : 'link'}
						onTouchStart={e => e.target.classList.add('active')}
						onTouchEnd={e => e.target.classList.remove('active')}
						onClick={nav}  
						data-page="Home">
						{
							'Home'.split('').map((letter, i) => {
								return (
									<div
										key={letter}
										className='link-letter'
										style={{
											transitionDelay: (d + (i/20)) + 's'
										}}>{letter}</div>
								);
							})
						}
						<div className='inner'>
							{
								'Home'.split('').map((letter, i) => {
									return (
										<div
											key={i}
											style={{
												transitionDelay: (d + (i/20)) + 's'
											}}>{letter}</div>
									);
								})
							}
						</div>
					</div>
					<div 
						className={page === 'Work' ? 'link on-page' : 'link'}
						onTouchStart={e => e.target.classList.add('active')}
						onTouchEnd={e => e.target.classList.remove('active')}
						onClick={nav}
						data-page="Work">
						{
							'Work'.split('').map((letter, i) => {
								return (
									<div
										key={i} 
										className='link-letter'
										style={{
											transitionDelay: (d + (i/20)) + 's'
										}}>{letter}</div>
								);
							})
						}
						<div className='inner'>
							{
								'Work'.split('').map((letter, i) => {
									return (
										<div
											key={i}
											style={{
												transitionDelay: (d + (i/20)) + 's'
											}}>{letter}</div>
									);
								})
							}
						</div>
					</div>
					<div 
						className={page === 'Contact' ? 'link on-page' : 'link'}
						onTouchStart={e => e.target.classList.add('active')}
						onTouchEnd={e => e.target.classList.remove('active')}
						onClick={nav}
						data-page="Contact">
						{
							'Contact'.split('').map((letter, i) => {
								return (
									<div
										key={i}
										className='link-letter'
										style={{
											transitionDelay: (d + (i/20)) + 's'
										}}>{letter}</div>
								);
							})
						}
						<div className='inner'>
							{
								'Contact'.split('').map((letter, i) => {
									return (
										<div
											key={i}
											style={{
												transitionDelay: (d + (i/20)) + 's'
											}}>{letter}</div>
									);
								})
							}
						</div>
					</div>
				</div>
			</section>
		);
	}
}


