import React, { Component } from 'react';
import Button3D from '../button3D/Button3D';
import BackgroundText from '../backgroundText/BackgroundText';

export default class Home extends Component {
	
	componentDidMount = () => {
		if(this.props.pastInitLoad) setTimeout(this.props.loaded, 500);
	}

	componentWillUnmount = () => this.props.removeHomeClasses();

	shouldComponentUpdate = nextProps => {
		const { classes, transDur, moveX } = nextProps;
		if(classes !== this.props.classes) return true;
		else if(transDur !== this.props.transDur) return true;
		else if(moveX !== this.props.moveX) return true;
		else return false;
	}

	render = () => {
		const { handlePageMove, handlePageEnter, classes, transDur, nav, moveX } = this.props;
		let inc = 7, inc2 = 0;
		return (
			<section 
				onMouseMove={handlePageMove}
				onMouseEnter={handlePageEnter}
				onTouchMove={handlePageMove}
				onTouchStart={handlePageEnter}
				className={classes}
				style={{
					backgroundPosition: `${moveX}% 100%`,
					transition: `background-position ${transDur} 0s, transform 0.5s 0s`
				}}>
				<BackgroundText
					text="Alex"
					incrementor={0} />
				<BackgroundText
					text="Figliolia"
					incrementor={0} />
				<div>
					<div>
						<h2>
							{
								'Front End'.split('').map((letter, i) => {
									if(letter === ' ') {
										return (
											<div 
												key={i}
												className="space"
												style={{
													transitionDelay: inc/140 + 's'
												}}>&nbsp;&nbsp;</div>
										);
									} else {
										inc-=i;
										return (
											<div 
												key={i}
												style={{
													transitionDelay: inc/140 + 's'
												}}>{letter}</div>
										);
									}
								})
							}
						</h2>
						<h2>
							{
								'Developer'.split('').map((letter, i) => {
									inc2 += i;
									return (
										<div 
											key={i}
											style={{
												transitionDelay: inc2/140 + 's'
											}}>{letter}</div>
									);
								})
							}
						</h2>
					</div>
					<Button3D 
						text="Work"
						function={nav} />
				</div>
			</section>
		);
	}
}



