import React, { Component } from 'react';
import Button3D from '../button3D/Button3D';
import BackgroundText from '../backgroundText/BackgroundText';

export default class Contact extends Component {

	componentDidMount = () => setTimeout(this.props.loaded, 500);

	componentWillUnmount = () => this.props.removeContactClasses();

	shouldComponentUpdate = nextProps => {
		const { classes, transDur, moveX } = nextProps;
		if(classes !== this.props.classes) return true;
		else if(transDur !== this.props.transDur) return true;
		else if(moveX !== this.props.moveX) return true;
		else return false;
	}

	render = () => {
		const { handlePageMove, handlePageEnter, classes, moveX, transDur } = this.props;
		return (
			<section 
				onMouseMove={handlePageMove}
				onMouseEnter={handlePageEnter}
				onTouchMove={handlePageMove}
				onTouchStart={handlePageEnter}
				className={classes}
				style={{
					backgroundPosition: `${moveX}% 100%`,
					transition: `background-position ${transDur} 0s, transform 0.5s 0s`
				}}>
				<BackgroundText
					text="Alex"
					incrementor={0} />
				<BackgroundText
					text="Figliolia"
					incrementor={0} />
				<div>
					<div>
						<h2>
							{
								'Contact'.split('').map((letter, i) => {
									return (
										<div
											style={{
												transition: `transform 0.5s ${i/20}s`
											}} 
											key={i}>{letter}</div>
									);
								})
							}
						</h2>
					</div>
					<Button3D 
						text="Call Me"
						function={(null)} />
					<Button3D 
						text="Email Me"
						function={() => window.location.href = "mailto:alexfigliolia@gmail.com.com?subject=Subject"} />
					<Button3D 
						text="GitHub"
						function={() => window.open('https://github.com/alexfigliolia', '_blank')} />
				</div>
			</section>
		);
	}
}


