import React, { Component } from 'react';

export default class Slide extends Component {
  constructor(props) {
    super(props);
   	this.state = { infoClasses: "info" }
  }

  componentDidMount = () => {
  	if(this.props.index === 0) {
  		setTimeout(() => { this.refs[0].classList.add('slide-reset'); }, 1500);
  	}
  }

  componentWillReceiveProps = nextProps => {
  	const { index } = this.props;
  	const { width, selectedIndex } = nextProps;
  	if(width >= 957 && this.state.infoClasses === "info change-info") {
  		this.setState({ infoClasses: "info" });
  	}
  	setTimeout(() => {
  		if(selectedIndex === index) {
  			this.refs[index].classList.add('slide-reset');
  		} else {
  			this.refs[index].classList.remove('slide-reset');
  			this.setState({ infoClasses: "info" });
  		}
	  }, 1500);
  }

  shouldComponentUpdate = (nextProps, nextState) => {
  	const { infoClasses } = this.state;
  	const { index, selectedIndex, moveX, width } = nextProps;
  	const curProps = this.props;
  	if(infoClasses !== nextState.infoClasses) return true;
  	else if(selectedIndex !== curProps.selectedIndex) return true;
  	else if(moveX !== curProps.moveX && selectedIndex === index) return true;
  	else if(width !== curProps.width) return true;
  	else return false;
  }

  visit = e => window.open(e.target.dataset.url, '_blank');

  switchText = () => {
  	this.setState((prevState, prevProps) => {
  		return {
  			infoClasses: prevState.infoClasses === "info" ? "info change-info" : "info"
  		}
  	});
  }

  render = () => {
  	const { index, selectedIndex, moveX, name, width, url, p1, p2 } = this.props;
    return (
    	<div
				ref={index}
				key={index}
				className="project"
				style={{
					backgroundPositionX: selectedIndex === index ? `${moveX}%` : '50%'
				}}>
				<div 
					id='snow' 
					style={{
						display: selectedIndex === 8 ? "block" : "none"
					}}></div>
				<div>
					<h2>
						{
							name.split('').map((letter, i) => {
								return (letter === ' ') ? <div key={i}>&nbsp;&nbsp;&nbsp;</div> : <div style={{transitionDelay: (0.3 + i/20) + 's'}} key={i}>{letter}</div>
							})
						}
					</h2>
					<div className={this.state.infoClasses}>
						<p>
							{p1}
							<a onClick={this.switchText}>&nbsp;More Info</a>
						</p>
						<p className={width < 957 ? "text-switch" : ""}>
							{p2}
							<a onClick={this.switchText}>&nbsp;Back</a>
						</p>
					</div>
					<button 
						onClick={this.visit} 
						data-url={url}>
						<h3 data-url={url}>Visit</h3>
					</button>
				</div>
			</div>
    );
  }
}



