import React, { PureComponent } from 'react';
import Header from './components/header/Header';
import Home from './components/home/Home';
import Menu from './components/menu/Menu';
import Loader from './components/loader/Loader';
import Work from './components/work/Work';
import Contact from './components/contact/Contact';

export default class App extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      height: window.innerHeight,
      width: window.innerWidth,
      appClasses: 'App App-drop App-rotate',
      burgerClasses: "hamburglar is-open",
      burgerToggle: true,
      menuClasses: "menu",
      loaderClasses: "loader loader-show",
      homeClasses: "home",
      contactClasses: "home contact",
      page: "Home",
      pastInitLoad: false,
      moveX: null,
      transDur: '0s'
    }
  }

  componentDidMount = () => {
    window.addEventListener('resize', this.updateHeight);
    if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) && window.DeviceOrientationEvent){
      window.addEventListener('deviceorientation', this.updateMoveX, true);
    } 
    setTimeout(() => { 
      this.setState({ appClasses: 'App App-drop', loaderClasses: 'loader' });
      setTimeout(() => {
        this.setHomeClasses();
        setTimeout(() => { this.setState({ appClasses: 'App', pastInitLoad: true }); }, 600);
      }, 400);
    }, 1000);
  }

  componentWillUnmount = () => {
    if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) && window.DeviceOrientationEvent){
      window.removeEventListener('deviceorientation', this.updateMoveX, true);
    } 
  }

  updateMoveX = e => this.setState({ moveX: ((e.gamma + 90) * 100) / 180 });

  updateHeight = () => this.setState({height: window.innerHeight, width: window.innerWidth });

  handlePageMove = e => {
    if(!window.DeviceOrientationEvent) {
      const x = e.type === 'mousemove' ? e.clientX : e.touches[0].clientX;
      const w = window.innerWidth;
      this.setState({ moveX: (x * 100) / w, transDur: '0s'});
    }
  }
  
  handlePageEnter = e => {
    if(!window.DeviceOrientationEvent) {
      const x = e.type === 'mouseenter' ? e.clientX : e.touches[0].clientX;
      const w = window.innerWidth;
      this.setState({ moveX: (x * 100) / w, transDur: '0.5s'});
    }
  }

  toggleBurger = () => {
    this.setState(prevState => {
      const { burgerClasses, burgerToggle, menuClasses } = prevState;
      return {
        burgerToggle : !burgerToggle,
        burgerClasses : (burgerClasses === "hamburglar is-closed") ? 
                          "hamburglar is-open" : 
                          "hamburglar is-closed",
        menuClasses: (menuClasses === "menu") ?
                      "menu menu-show" :
                      "menu"
      }
    });
  }

  setHomeClasses = () => {
    setTimeout(() => {  this.setState({homeClasses: 'home home-show'}); }, 1000);
    setTimeout(() => { this.setState({homeClasses: 'home home-show home-reset-delays'}); }, 1500);
  }

  setContactClasses = () => {
    setTimeout(() => {  this.setState({contactClasses: 'home contact contact-show'}); }, 1000);
    setTimeout(() => { this.setState({contactClasses: 'home contact contact-show contact-reset-delays'}); }, 1500);
  }

  removeHomeClasses = () => {
    setTimeout(() => { this.setState({homeClasses: 'home'}) }, 1500);
  }

  removeContactClasses = () => {
    setTimeout(() => { this.setState({contactClasses: 'home contact'}) }, 1500);
  }

  navigate = (e) => {
    const p = e.target.dataset.page;
    const int = this.state.burgerToggle ? 0 : 800;
    if(int > 0) this.toggleBurger();
    setTimeout(() => { this.setState({ appClasses: 'App App-drop' }) }, int);
    setTimeout(() => { this.setState({ appClasses: 'App App-drop App-rotate'}) }, int + 750);
    setTimeout(() => { this.setState({ loaderClasses: 'loader loader-show' }) }, int + 900);
    setTimeout(() => { this.setState({ page: p }) }, int + 1500);
    setTimeout(() => { this.setState({ appClasses: 'App App-drop' }) }, int + 2000);
    setTimeout(() => { this.setState({ loaderClasses: 'loader' }) }, int + 2200);
    setTimeout(() => { this.setState({ appClasses: 'App' }) }, int + 2750);
  }

  render = () => {
    const { 
      appClasses, 
      height, 
      width, 
      page, 
      loaderClasses, 
      burgerClasses, 
      homeClasses, 
      pastInitLoad, 
      moveX, 
      transDur, 
      contactClasses,
      menuClasses
    } = this.state;
    return (
      <div className={appClasses} style={{height}}>

        <div className='back' style={{height}}>
          <Loader 
            classes={loaderClasses} />
        </div>

        <div className='front' style={{height}}>
          <Header 
            burgerStuff={burgerClasses}
            burger={this.toggleBurger}
            nav={this.navigate} />

          {
            page === "Home" &&
            <Home
              classes={homeClasses}
              pastInitLoad={pastInitLoad}
              moveX={moveX}
              transDur={transDur}
              removeHomeClasses={this.removeHomeClasses}
              nav={this.navigate}
              loaded={this.setHomeClasses}
              handlePageMove={this.handlePageMove}
              handlePageEnter={this.handlePageEnter} />
          }

          {
            page === "Work" &&
            <Work
              height={height}
              moveX={moveX}
              transDur={transDur}
              width={width} />
          }
          
          {
            page === "Contact" &&
            <Contact
              classes={contactClasses}
              moveX={moveX}
              transDur={transDur}
              loaded={this.setContactClasses}
              removeContactClasses={this.removeContactClasses}
              handlePageMove={this.handlePageMove}
              handlePageEnter={this.handlePageEnter} />
          }
          <Menu 
            classes={menuClasses}
            page={page}
            nav={this.navigate} />
        </div>
      </div>
    );
  }
}



