import React, { Component } from 'react';
import Flickity from 'flickity';
import { api } from './workApi';
import Slide from './Slide';

export default class Work extends Component {
	constructor(props) {
		super(props);
		this.state = {
			selectedIndex: 0,
			infoClasses: "info"
		}
	}
	componentDidMount = () => {
    const carousel = document.getElementById('slider');
    const options = {
      cellSelector: '.project',
      contain: true,
      initialIndex: 0,
      accessibility: true,
      prevNextButtons: true,
      wrapAround: true,
      pageDots: false,
      selectedAttraction: 0.15,
			friction: 0.8
    }
		this.flkty = new Flickity(carousel, options);
    this.flkty.on('cellSelect', () => {
    	this.setState({ selectedIndex: this.flkty.selectedIndex });
    });
  }

  componentWillUnmount = () => {
    if (this.flkty) {
      this.flkty.off('cellSelect', this.updateSelected);
      this.flkty.destroy();
    }
  }

  shouldComponentUpdate = (nextProps, nextState) => {
  	const { selectedIndex, infoClasses } = nextState;
  	const { moveX, height, width, transDur } = nextProps;
  	if(selectedIndex !== this.state.selectedIndex) return true;
  	else if(infoClasses !== this.state.infoClasses) return true;
  	else if(moveX !== this.props.moveX) return true;
  	else if(height !== this.props.height) return true;
  	else if(width !== this.props.width) return true;
  	else if(transDur !== this.props.transDur) return true;
  	else return false;  
  }

	render = () => {
		const { selectedIndex } = this.state;
		const { moveX, width, height } = this.props; 
		return (
			<div className='work-wrapper' style={{height}}>
				<section className="work" id='slider' style={{height}}>
					{
						api.map((obj, i) => {
							const { name, p1, p2, url } = obj; 
							return (
								<Slide
									index={i}
									key={i}
									name={name}
									selectedIndex={selectedIndex}
									moveX={moveX}
									width={width}
									url={url}
									p1={p1}
									p2={p2} />
							);
						})
					}

				</section>
			</div>
		);
	}
}


